<template>
    <div id="hue-app">
        <h2>PHILIPS HUE</h2>
        <p>{{message.bridge}}</p>
        <div v-if="user">
            <div v-for="(light, index) in lights" :key="index">
                <div v-if="light.state.reachable === true">
                    <h4>{{light.name}}</h4>
                    <div class="container-light">
                        <a class="btn-switch" @click="switchLight(index, !light.state.on)">
                            {{light.state.on === false ? 'Light on' : 'Light off'}}
                        </a>
                        <circle-slider
                                @input="changeBrightness(index)"
                                :min="1"
                                :side="250"
                                :circleColor="'#ffffff'"
                                :progressColor="'#334860'"
                                :knobColor="'#ffffff'"
                                :max="254"
                                :knob-radius="10"
                                :circle-width="2"
                                :progress-width="7"
                                v-model="light.state.bri">
                        </circle-slider>
                    </div>
                    <div v-if="light.state.on === true">
                        <span class="label-bright">{{ Math.ceil(light.state.bri / 254 * 100)}} % <br/></span>
                        Brightness
                    </div>
                    <div v-else>
                        Off
                    </div>
                </div>
            </div>
            <div v-if="!lights[1]">
                No light found
            </div>
        </div>
    </div>
</template>

<script>
  /* global CacheManager */
  import jsHue from "jshue"
  // import {CacheManager} from 'reflectos-sdk'
  import {CircleSlider} from 'vue-circle-slider'
  import _ from 'lodash';

  export default {
    name: 'hue',
    components: {
      CircleSlider
    },
    props: {},
    data() {
      return {
        message: {
          bridge: 'Searching for bridge...'
        },
        ips: [],
        user: null,
        cacheManager: new CacheManager('hue'),
        brightness: 254,
        index: 0,
        lights: [],
        timeout: null
      }
    },
    watch: {
      brightness: _.debounce(function () {
        this.user.setLightState(this.index, {bri: this.brightness}).then(data => {
        });
      }, 1000)
    },
    mounted() {
      this.cacheManager.store('hue', 'hue')

      let hue = jsHue();

      hue.discover().then(bridges => {
        if (bridges.length === 0) {
          this.message.bridge = 'No bridge found. :('
        }
        else {
          bridges.forEach(b => this.ips.push(b.internalipaddress));
        }

        this.ips.forEach(ip => this.message.bridge = 'Bridge has been found at IP : ' + ip + ', please press button');

        let bridge = hue.bridge(this.ips[0]);


        if (this.cacheManager.get('username')) {
          this.user = bridge.user(this.cacheManager.get('username'));
          this.user.getLights().then(data => {
            this.lights = data;
            // for (let key in this.lights) {
            //   console.log(this.lights[key].state.bri);
            // }
            // console.log(this.lights);
          });
          this.message = '';
        }
        else {
          let interval = setInterval(() => {
            bridge.createUser('ureflect_app#testdevice').then(data => {

              if (data[0].error) {
              }
              else {
                let username = data[0].success.username;

                this.cacheManager.store('username', username);

                this.user = bridge.user(username);

                this.user.getLights().then(data => {
                  this.lights = data;
                });

                this.message = ''
                clearInterval(interval);
              }
            });

          }, 3000);
        }
        setInterval(() => {
          if (this.user) {
            this.user.getLights().then(data => {
              this.lights = data;
            });
          }
        }, 3000);
      }).catch(e => this.message.bridge = 'Error finding bridges ' + e);
    },
    methods: {
      switchLight(index, state) {
        this.user.setLightState(index, {on: state}).then(data => {
          this.lights[index].state.on = state;
        });
      },
      changeBrightness(index) {
        this.index = index;
        this.brightness = this.lights[index].state.bri;
      },
    },
  }
</script>

<style scoped>
    p, div {
        color: white;
    }

    .label-bright {
        font-size: 25px;
    }

    .btn-switch {
        position: absolute;
        top: calc(50% - 22px);
        left: calc(50% - 50px);
        border: 1px solid white;
        padding: 10px 20px;
    }

    .container-light {
        position: relative;
    }

</style>
